.title svg {
  height: 45px;
  width: 165px;
  position: relative; }
  .title svg .word {
    font-family: 'Tangerine', cursive;
    letter-spacing: 0.05em;
    font-size: 50px;
    font-weight: bold; }
    @media (max-width: 768px) {
      .title svg .word {
        font-size: 45px; } }
    @media (max-width: 400px) {
      .title svg .word {
        font-size: 40px; } }



